<template>
  <div class="game-main">
    <div class="game-info">
      <ul class="clear-fix">
        <li>
          <div class="logo">
            <a :class="{ 'no-bg': !isShowColorInLogo }" href="javascript:;">
              <el-image
                :src="gameDetail.logo"
                fit="scale-down"
                class="betting-lottery-logo"
              >
                <div slot="error" class="error-image-slot"></div>
              </el-image>
            </a>
          </div>
        </li>
        <li>
          <div class="lottery-time">
            <div class="left">
              <div class="white-bg">
                {{ $t("gameInfo.the") }}
                <i class="text_color">{{ sscAwardInfo["issue"] }}</i>
                {{ $t("gameInfo.stage") }}
              </div>
              <div v-if="openPromptVoice !== -1">
                <p class="white-bg">{{ $t("gameInfo.lotteryTime") }}</p>
                <span
                  class="voice text_color"
                  @click="setVolumeWinFlagFn('volum')"
                >
                  <i v-if="openPromptVoice" class="icon-volume-medi"></i>
                  <i v-else class="icon-volume-mute2"></i>
                </span>
              </div>
            </div>
            <div class="time">
              <span v-if="+endTimeInfo.day > 0" class="day">{{
                endTimeInfo.day
              }}</span>
              <i v-if="+endTimeInfo.day > 0">:</i>
              <span class="hour">{{ endTimeInfo.hour }}</span>
              <i>:</i>
              <span class="minute">{{ endTimeInfo.min }}</span>
              <i>:</i>
              <span class="second">{{ endTimeInfo.sec }}</span>
            </div>
          </div>
        </li>
        <li>
          <div class="lottery-number">
            <div class="left">
              <div class="white-bg">
                {{ $t("gameInfo.the") }}
                <i class="text_color">{{ sscLastWnNumber["issue"] }}</i>
                {{ $t("gameInfo.stage") }}
              </div>
              <div v-if="openPromptVoice !== -1">
                <p class="white-bg">{{ $t("gameInfo.lotteryNumber") }}</p>
                <el-switch v-model="winningReminder" inactive-color="#dcdfe6">
                </el-switch>
              </div>
            </div>
            <div class="ball-list">
              <span
                v-for="(item, index) in wnNumberList"
                :class="{ [item.className]: true }"
                :key="index"
                class="number-ball text_color"
                >{{ item.label }}</span
              >
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { lotteryMixin } from "../../../common/js/mixin";
import { getAjaxMixins } from "../../../common/js/getAjaxMixins";

export default {
  mixins: [lotteryMixin, getAjaxMixins],
  data() {
    return {};
  },
  methods: {},
  computed: {
    ...mapGetters({
      menuList: "menu"
    }),
    ...mapGetters([
      "isChoiceShow",
      "szcpMenuList",
      "lotteryId",
      "sscAwardInfo",
      "sscLastWnNumber",
      "userInfo",
      "gameDetail"
    ]),
    wnNumberList() {
      let wnNumber = this.sscLastWnNumber["wn_number"];
      if (wnNumber && wnNumber.length) {
        wnNumber = wnNumber
          .replace("+", " ")
          .split(" ")
          .map((wnNumber, numberIndex) => {
            if (numberIndex < 5) {
              return {
                label: wnNumber,
                className: "red"
              };
            } else {
              return {
                label: wnNumber,
                className: "blue"
              };
            }
          });
      } else {
        wnNumber = [];
        for (let i = 0; i < 6; i++) {
          wnNumber.push({
            label: "??",
            className: i === 5 ? "blue" : "red"
          });
        }
      }
      return wnNumber;
    }
  },
  created() {}
};
</script>

<style scoped>
@import "../statics/css/bettingTray.css";
.game-info > ul > li .lottery-number .ball-list span {
  width: 41px;
  height: 41px;
  display: inline-block;
  border-radius: 50%;
  line-height: 35px;
  text-align: center;
  font-size: 20px;
  background-image: linear-gradient(0deg, #eaa84b 0%, #ffffb9 100%),
    linear-gradient(#ffffff, #ffffff);
  background-blend-mode: normal, normal;
  box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.3);
  border: solid 2px #9f6900;
  margin: 2px;
}
.game-info > ul > li .lottery-number .ball-list span.green {
  background: url("../../../statics/img/lottery-number-lhc-green.png") no-repeat -2px -4px;
  color: #fff !important;
}
.game-info > ul > li .lottery-number .ball-list span.blue {
  background: url("../../../statics/img/lottery-number-lhc-blue.png") no-repeat -2px -4px;
  color: #fff !important;
}
.game-info > ul > li .lottery-number .ball-list span.red {
  background: url("../../../statics/img/lottery-number-lhc-red.png") no-repeat -2px -4px;
  color: #fff !important;
}
.game-info > ul > li .lottery-number .ball-list span:nth-last-of-type(1) {
  margin-left: 26px;
  position: relative;
}
.game-info
  > ul
  > li
  .lottery-number
  .ball-list
  span:nth-last-of-type(1)::after {
  content: "+";
  display: block;
  position: absolute;
  font-size: 20px;
  font-weight: bold;
  top: 0;
  left: -23px;
}
</style>

